html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--dark-grayish-blue);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-size: 0.9375rem;
}

:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --grayish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

@media (min-width: 64em) {
  body {
    padding: 5rem 7.5rem;
  }
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 0.75rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
}
.navbar__menu a {
  margin: 0 0.9375rem;
  color: var(--dark-grayish-blue);
  font-size: 0.9375rem;
}
.navbar__menu a:last-child {
  margin-right: 0;
}

.main-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 2fr 1fr 0.5fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.main-section__image {
  grid-area: 1/1/2/3;
  width: 100%; /* or any custom size */
  height: 100%;
  object-fit: contain;
}
.main-section__title {
  grid-area: 2/1/4/2;
  margin-top: 1.875rem;
}
.main-section__subtitle {
  grid-area: 2/2/3/3;
  margin-top: 1.875rem;
}
.main-section__button {
  grid-area: 3/2/4/3;
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  background-color: var(--soft-red);
  width: 50%;
  height: 60%;
}
.main-section__button p {
  text-align: center;
  letter-spacing: 0.1875rem;
  text-transform: uppercase;
  font-size: 0.625rem;
  font-weight: 600;
  color: var(--off-white);
}

.center-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 2fr;
  grid-column-gap: 1.5625rem;
  grid-row-gap: 0px;
}
.center-container .main-section {
  grid-area: 1/1/2/3;
}
.center-container .aside-section {
  grid-area: 1/3/2/4;
}

.bottom-section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 2.1875rem;
  grid-row-gap: 0px;
}
.bottom-section__01 {
  grid-area: 1/1/2/2;
}
.bottom-section__02 {
  grid-area: 1/2/2/3;
}
.bottom-section__03 {
  grid-area: 1/3/2/4;
}
.bottom-section__grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 1.5625rem;
  grid-row-gap: 0px;
}
.bottom-section__grid-container img {
  grid-area: 1/1/2/2;
  width: 100%; /* or any custom size */
  height: 100%;
  object-fit: contain;
}
.bottom-section__grid-container div {
  grid-area: 1/2/2/4;
}

.aside-section {
  background-color: var(--very-dark-blue);
  padding: 1.5625rem;
}